section{display:flex;flex-direction:column;align-items:center;gap:var(--margin-row);width:100%;padding:var(--margin-row) var(--margin);background:var(--background-color)}section#date{padding-bottom:calc(var(--margin-row)*3)}@media screen and (max-width: 1024px){section#date{padding-bottom:calc(var(--margin-row)*2)}}section#gallery{color:var(--white-color);background:var(--foreground-color);overflow:hidden}section#gallery .text{opacity:.5}section#map img{width:100vw}